.main{width:100%;display:flex;justify-content:center;background:var(--bg1, linear-gradient(102deg, #2C2C2C 1.56%, #1C1C1C 95.55%));padding:50px 10px 50px 10px}.container{width:1000px}.container .item{display:flex;background:var(--Gray2, linear-gradient(131deg, #414141 0%, #2D2D2D 100.52%));box-shadow:0px 4px 15px 0px rgba(27,27,27,.28);padding:30px;border-radius:30px;max-width:100%;margin:0 0 25px 0}.container .item:last-child{margin:0}.container .item:hover{filter:drop-shadow(3px 3px 10px rgb(255, 255, 255));transition:.2s ease-in-out}.container .linkWrapper{display:flex;flex-direction:column;justify-content:center}.container .link{display:block;min-width:270px;max-width:270px;min-height:250px}.container .photoSite{max-width:100%;height:250px;border-radius:30px}.container .description{flex-grow:1;margin:0 0 0 30px}.container .descriptionTitle{color:#fff;font-size:16px;font-weight:300;text-align:center;margin:0 0 15px 0}.container .descriptionList{list-style-type:"≻";color:#fff;font-size:14px}.container .descriptionText{margin:0 0 10px 10px}.container .descriptionBtn{text-align:center}.container .descriptionBtn .descriptionLink{display:inline-block;padding:10px 20px;font-size:16px;background-color:#676767;color:#fff;border:none;border-radius:5px;cursor:pointer;transition:background-color .3s,transform .2s;margin:0 0 15px 15px}.container .descriptionBtn .descriptionLink:hover{background-color:#474747;box-shadow:0px 0px 10px rgba(0,0,0,.2)}@media(min-width: 320px)and (max-width: 800px){.container .item{display:block}.container .linkWrapper{margin:0 0 15px 0}.container .link{max-width:275px;min-height:200px;align-self:center}.container .photoSite{height:200px}.container .descriptionTitle{font-size:14px}.container .descriptionList{max-width:450px;margin:0 auto;font-size:12px}.container .descriptionBtn{text-align:center}.container .descriptionBtn .descriptionLink{font-size:12px;padding:7px 12px}}